import { Component, OnInit, Input, ViewChild } from '@angular/core';
import {Router, RouterOutletMap} from '@angular/router';
import {Location} from '@angular/common';
import {CycavatarComponent} from '../cycavatar/Cycavatar.component';


@Component({
	moduleId: module.id,
	selector: 'media-plan-progress',
	templateUrl: 'planprogress.component.html',
	styleUrls: ['planprogress.component.css'],
	directives: [CycavatarComponent]
})
export class PlanprogressComponent implements OnInit {

	@Input()
	private showAppHeader: boolean;

	@ViewChild(CycavatarComponent)
	private cycAvatarComponent: CycavatarComponent;
	private completionStage: any = {};
	private active: any = {};
	private animation: number;

	public navigateTo(name: string) {
		this._router.navigate([name]);
	}

	private continueAnimation() {
		let ref: any = this;
		ref.cycAvatarComponent.startQueryAnimation();
	}

	constructor(private _router: Router, private _location: Location, private _routerOutletMap: RouterOutletMap) {
		this._location.subscribe(
			(x) => {
				if ("finishedStep1" in x) {
					this.completionStage.finishedStep1 = x.finishedStep1;
					this.active.mediaPlan = true;
				}
				if ("finishedStep2" in x) {
					this.completionStage.finishedStep2 = x.finishedStep2;
					this.active.mediaPlan = true;
				}
				if ("finishedStep3" in x) {
					this.completionStage.finishedStep3 = x.finishedStep3;
					this.active.mediaPlan = true;
				}
				if ("finishedStep4" in x) {
					this.completionStage.finishedStep4 = x.finishedStep4;
					this.active.mediaPlan = true;
				}
				if ("finishedStep5" in x) {
					this.completionStage.finishedStep5 = x.finishedStep5;
					this.active.mediaPlan = true;
				}
				if ("exportData" in x) {
					this.active.mediaPlan = false;
					this.active.concepts = false;
					this.active.mri = false;
					this.active.export = true;
					this.cycAvatarComponent.updateAvatar();
					this.cycAvatarComponent.startQueryAnimation();
				}
				if ("stopAnimation" in x) {
					this.animation = 0;
				}
				if ("startAnimation" in x) {
					this.animation = 1;
					this.continueAnimation();
				}
			}
		);
		this._router.changes.subscribe(
			() => {
				if (this._location.path().startsWith("/clientbrief")) {
					this.showAppHeader = true;
				} else if (this._location.path().startsWith("/mediaplan")) {
					this.completionStage = {};
					this.active.mediaPlan = false;
					this.active.mediaPlan = true;
					this.active.concepts = false;
					this.showAppHeader = false;
					this.active.mri = false;
					this.active.export = false;
					this.cycAvatarComponent.displayAvatar();
				} else if (this._location.path().startsWith("/concepts")) {
					this.active.mediaPlan = false;
					this.showAppHeader = false;
					this.active.mediaPlan = false;
					this.active.concepts = true;
					this.active.mri = false;
					this.active.export = false;
					this.cycAvatarComponent.updateAvatar();
					// this.cycAvatarComponent.startQueryAnimation();
					// this.interval = setInterval(this.cycAvatarComponent.startQueryAnimation,3000);					
				} else if (this._location.path().startsWith("/suggestion")) {
					this.showAppHeader = false;
					this.active.mediaPlan = false;
					this.active.concepts = false;
					this.active.mri = true;
					this.active.export = false;
					this.cycAvatarComponent.updateAvatar();
					this.cycAvatarComponent.startQueryAnimation();
				}
			}
		);
	}

	ngOnInit() {
		this.active.mediaPlan = false;
		this.active.concepts = false;
		this.active.mri = false;
		this.active.export = false;
	}

}
